import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CdkTableModule } from '@angular/cdk/table';

import {
    MatButtonModule,
    MatCheckboxModule,
    MatNativeDateModule,
    MatDatepickerModule,
    MatDialogModule,
    MatFormFieldModule,
    MatIconModule,
    MatInputModule,
    MatMenuModule,
    MatPaginatorModule,
    MatRippleModule,
    MatSidenavModule,
    MatSortModule,
    MatTableModule,
    MatToolbarModule
} from '@angular/material';

import { FuseSharedModule } from '@fuse/shared.module';
import { FuseConfirmDialogModule } from '@fuse/components';

import { FuseReceivingsComponent } from './receivings.component';
import { ReceivingsService } from './receivings.service';
import { FuseReceivingsReceivingListComponent } from './receiving-list/receiving-list.component';
import { FuseReceivingsSelectedBarComponent } from './selected-bar/selected-bar.component';
import { ReceivingFormDialogComponent } from './receiving-form/receiving-form.component';

const routes: Routes = [
    {
        path     : '**',
        component: FuseReceivingsComponent,
        resolve  : {
            receivings: ReceivingsService
        }
    }
];

@NgModule({
    declarations   : [
        FuseReceivingsComponent,
        FuseReceivingsReceivingListComponent,
        FuseReceivingsSelectedBarComponent,
        ReceivingFormDialogComponent
    ],
    imports        : [
        RouterModule.forChild(routes),
        CdkTableModule,

        MatButtonModule,
        MatCheckboxModule,
        MatNativeDateModule,
        MatDatepickerModule,
        MatDialogModule,
        MatFormFieldModule,
        MatIconModule,
        MatInputModule,
        MatMenuModule,
        MatPaginatorModule,
        MatRippleModule,
        MatSidenavModule,
        MatSortModule,
        MatTableModule,
        MatToolbarModule,

        FuseSharedModule,
        FuseConfirmDialogModule
    ],
    providers      : [
        ReceivingsService
    ],
    entryComponents: [ReceivingFormDialogComponent]
})
export class FuseReceivingsModule
{
}



