import { FuseUtils } from '@fuse/utils';

export class Receiving
{
    id: string;
    poNumber: string;
    vendorName: string;
    customerName: string;
    identity: string;
    orderDate: string;
    inHandsDate: string;
    productNumber: string;
    sku: string;
    color: string;
    size: string;
    quantity: string;
    site: string;
    bin: string;
    status: string;

    constructor(receiving)
    {
        {
            this.id = receiving.id || FuseUtils.generateGUID();
            this.poNumber = receiving.poNumber || '';
            this.vendorName = receiving.vendorName || '';
            this.customerName = receiving.customerName || '';
            this.identity = receiving.identity || '';
            this.orderDate = receiving.orderDate || '';
            this.inHandsDate = receiving.inHandsDate || '';
        }
    }
}



