import { Component, OnDestroy, OnInit, TemplateRef, ViewChild, ViewEncapsulation } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { MatDialog, MatDialogRef, MatPaginator, MatSort } from '@angular/material';
import { DataSource } from '@angular/cdk/collections';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { ReceivingFormDialogComponent } from '../receiving-form/receiving-form.component';

import { ReceivingsService } from '../receivings.service';

import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import { Receiving } from '../receiving.model';

@Component({
    selector     : 'fuse-receivings-receiving-list',
    templateUrl  : './receiving-list.component.html',
    styleUrls    : ['./receiving-list.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations   : fuseAnimations
})
export class FuseReceivingsReceivingListComponent implements OnInit, OnDestroy
{
    @ViewChild('dialogContent') dialogContent: TemplateRef<any>;
    @ViewChild(MatPaginator) paginator: MatPaginator;
    @ViewChild(MatSort) sort: MatSort;

    receivings: any;
    dataSource: ReceivingsDataSource | null;
    displayedColumns = ['checkbox', 'poNumber', 'vendorName', 'customerName', 'identity', 'orderDate', 'inHandsDate', 'productNumber', 'buttons'];
    selectedReceivings: any[];
    checkboxes: {};

    onReceivingsChangedSubscription: Subscription;
    onSelectedReceivingsChangedSubscription: Subscription;

    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
    dialogRef: MatDialogRef<ReceivingFormDialogComponent>;

    constructor(
        private receivingsService: ReceivingsService,
        private router: Router,
        public dialog: MatDialog
    )
    {
        this.onReceivingsChangedSubscription =
            this.receivingsService.onReceivingsChanged.subscribe(receivings => {

                this.receivings = receivings;

                this.checkboxes = {};
                receivings.map(receiving => {
                    this.checkboxes[receiving.id] = false;
                });
            });

        this.onSelectedReceivingsChangedSubscription =
            this.receivingsService.onSelectedReceivingsChanged.subscribe(selectedReceivings => {
                for ( const id in this.checkboxes )
                {
                    if ( !this.checkboxes.hasOwnProperty(id) )
                    {
                        continue;
                    }

                    this.checkboxes[id] = selectedReceivings.includes(id);
                }
                this.selectedReceivings = selectedReceivings;
            });

    }

    ngOnInit()
    {
        this.dataSource = new ReceivingsDataSource(
            this.receivingsService,
            this.paginator,
            this.sort
        );
    }

    ngOnDestroy()
    {
        this.onReceivingsChangedSubscription.unsubscribe();
        this.onSelectedReceivingsChangedSubscription.unsubscribe();
    }

    editReceiving(receiving)
    {
        this.dialogRef = this.dialog.open(ReceivingFormDialogComponent, {
            panelClass: 'receiving-form-dialog',
            data      : {
                receiving: receiving,
                action : 'edit'
            }
        });

        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response )
                {
                    return;
                }
                const actionType: string = response[0];
                const formData: FormGroup = response[1];
                switch ( actionType )
                {
                    /**
                     * Save
                     */
                    case 'save':

                        this.receivingsService.updateReceiving(
                            formData.getRawValue()
                        );

                        break;
                    /**
                     * Delete
                     */
                    case 'delete':

                        this.deleteReceiving(receiving);

                        break;
                }
            });
    }

    /**
     * Delete Receiving
     */
    deleteReceiving(receiving)
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                this.receivingsService.deleteReceiving(receiving);
            }
            this.confirmDialogRef = null;
        });

    }

    onSelectedChange(receivingId)
    {
        this.receivingsService.toggleSelectedReceiving(receivingId);
    }

    toggleAll(ev) {
        if (ev) {
            this.receivingsService.toggleSelectAll();
        }
    }
}

export class ReceivingsDataSource extends DataSource<any>
{
    total = 0;

    private sub1: Subscription;

    constructor(
        private receivingsService: ReceivingsService,
        private _paginator: MatPaginator,
        private _sort: MatSort
    ) {
        super();
    }

    /** Connect function called by the table to retrieve one stream containing the data to render. */
    connect(): Observable<Receiving[]>
    {
        const displayDataChanges = [
            this.receivingsService.onReceivingsChanged,
            this._paginator.page,
            this._sort.sortChange
        ];


        this.sub1 = this.receivingsService.onReceivingsChanged
            .subscribe(receivings => {
                setTimeout(() => {
                    this.total = receivings.length;
                });
            });

        return Observable.merge(...displayDataChanges)
            .map(() => {
                let receivings = this.receivingsService.receivings;

                receivings = this.sortData(receivings);

                const startIndex = this._paginator.pageIndex * this._paginator.pageSize;
                receivings = receivings.slice(startIndex, startIndex + this._paginator.pageSize);

                return receivings;
            });
    }

    sortData(data): Receiving[] {
        if ( !this._sort.active || this._sort.direction === '' )
        {
            return data;
        }

        return data.sort((a, b) => {
            let valueA, valueB;

            switch(this._sort.active) {
                case 'created':
                    valueA = new Date(a[this._sort.active]);
                    valueB = new Date(b[this._sort.active]);
                    break;
                case 'name':
                    valueA = a['firstname'] + ' ' + a['lastname'];
                    valueB = b['firstname'] + ' ' + b['lastname'];
                    break;
                default:
                    valueA = a[this._sort.active];
                    valueB = b[this._sort.active];
                    break;
            }

            return (valueA < valueB ? -1 : 1) * (this._sort.direction === 'asc' ? 1 : -1);
        });
    }

    disconnect()
    {
        this.sub1.unsubscribe();
    }
}



