import { Component } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';

import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';

import { ReceivingsService } from '../receivings.service';

@Component({
    selector   : 'fuse-selected-bar',
    templateUrl: './selected-bar.component.html',
    styleUrls  : ['./selected-bar.component.scss']
})
export class FuseReceivingsSelectedBarComponent
{
    selectedReceivings: string[];
    hasSelectedReceivings: boolean;
    isIndeterminate: boolean;
    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;

    constructor(
        private receivingsService: ReceivingsService,
        public dialog: MatDialog
    )
    {
        this.receivingsService.onSelectedReceivingsChanged
            .subscribe(selectedReceivings => {
                this.selectedReceivings = selectedReceivings;
                setTimeout(() => {
                    this.hasSelectedReceivings = selectedReceivings.length > 0;
                    this.isIndeterminate = (selectedReceivings.length !== this.receivingsService.receivings.length && selectedReceivings.length > 0);
                }, 0);
            });

    }

    selectAll()
    {
        this.receivingsService.selectReceivings();
    }

    deselectAll()
    {
        this.receivingsService.deselectReceivings();
    }

    deleteSelectedReceivings()
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete all selected receivings?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                this.receivingsService.deleteSelectedReceivings();
            }
            this.confirmDialogRef = null;
        });
    }

}



