import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Subject } from 'rxjs/Subject';

import { FuseUtils } from '@fuse/utils';

import { Receiving } from './receiving.model';

@Injectable()
export class ReceivingsService implements Resolve<any>
{
    onReceivingsChanged: BehaviorSubject<any> = new BehaviorSubject([]);
    onSelectedReceivingsChanged: BehaviorSubject<any> = new BehaviorSubject([]);
    onSearchTextChanged: Subject<any> = new Subject();
    onFilterChanged: Subject<any> = new Subject();

    receivings: Receiving[];
    selectedReceivings: string[] = [];

    searchText: string;
    filterBy: string;

    constructor(private http: HttpClient)
    {
    }

    /**
     * The Receivings App Main Resolver
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns {Observable<any> | Promise<any> | any}
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
    {
        return new Promise((resolve, reject) => {

            Promise.all([
                this.getReceivings(),
            ]).then(
                ([files]) => {

                    this.onSearchTextChanged.subscribe(searchText => {
                        this.searchText = searchText;
                        this.getReceivings();
                    });

                    this.onFilterChanged.subscribe(filter => {
                        this.filterBy = filter;
                        this.getReceivings();
                    });

                    resolve();

                },
                reject
            );
        });
    }

    getReceivings(): Promise<any>
    {
        return new Promise((resolve, reject) => {
                this.http.get('api/receivings')
                    .subscribe((response: any) => {

                        this.receivings = response;

                        if ( this.searchText && this.searchText !== '' )
                        {
                            this.receivings = FuseUtils.filterArrayByString(this.receivings, this.searchText);
                        }

                        this.receivings = this.receivings.map(receiving => {
                            return new Receiving(receiving);
                        });

                        this.onReceivingsChanged.next(this.receivings);
                        resolve(this.receivings);
                    }, reject);
            }
        );
    }

    /**
     * Toggle selected receiving by id
     * @param id
     */
    toggleSelectedReceiving(id)
    {
        // First, check if we already have that todo as selected...
        if ( this.selectedReceivings.length > 0 )
        {
            const index = this.selectedReceivings.indexOf(id);

            if ( index !== -1 )
            {
                this.selectedReceivings.splice(index, 1);

                // Trigger the next event
                this.onSelectedReceivingsChanged.next(this.selectedReceivings);

                // Return
                return;
            }
        }

        // If we don't have it, push as selected
        this.selectedReceivings.push(id);

        // Trigger the next event
        this.onSelectedReceivingsChanged.next(this.selectedReceivings);
    }

    /**
     * Toggle select all
     */
    toggleSelectAll()
    {
        if ( this.selectedReceivings.length === this.receivings.length )
        {
            this.deselectReceivings();
        }
        else
        {
            this.selectReceivings();
        }
    }

    selectReceivings(filterParameter?, filterValue?)
    {
        this.selectedReceivings = [];

        // If there is no filter, select all todos
        if ( filterParameter === undefined || filterValue === undefined )
        {
            this.selectedReceivings = [];
            this.receivings.map(receiving => {
                this.selectedReceivings.push(receiving.id);
            });
        }
        else
        {
            /* this.selectedReceivings.push(...
                 this.receivings.filter(todo => {
                     return todo[filterParameter] === filterValue;
                 })
             );*/
        }

        // Trigger the next event
        this.onSelectedReceivingsChanged.next(this.selectedReceivings);
    }

    updateReceiving(receiving)
    {
        return new Promise((resolve, reject) => {

            this.http.post('api/receivings/' + receiving.id, {...receiving})
                .subscribe(response => {
                    this.getReceivings();
                    resolve(response);
                });
        });
    }

    deselectReceivings()
    {
        this.selectedReceivings = [];

        // Trigger the next event
        this.onSelectedReceivingsChanged.next(this.selectedReceivings);
    }

    deleteReceiving(receiving)
    {
        const receivingIndex = this.receivings.indexOf(receiving);
        this.receivings.splice(receivingIndex, 1);
        this.onReceivingsChanged.next(this.receivings);
    }

    deleteSelectedReceivings()
    {
        for ( const receivingId of this.selectedReceivings )
        {
            const receiving = this.receivings.find(_receiving => {
                return _receiving.id === receivingId;
            });
            const receivingIndex = this.receivings.indexOf(receiving);
            this.receivings.splice(receivingIndex, 1);
        }
        this.onReceivingsChanged.next(this.receivings);
        this.deselectReceivings();
    }

}



